<template lang="pug">
.container
  h1.title Información del Contribuyente
  article.message.is-info
    .message-header
      p Información Tributaria
    .message-body
      .p
        strong Nombre Comercial:
        | {{ ' ' + tracks[0].nombreComercial }}
      .p
        strong Razón Social:
        | {{ ' ' + tracks[0].razonSocial }}
      .p
        strong RUC:
        | {{ ' ' + tracks[0].ruc }}
      .p
        strong Dirección:
        | {{ ' ' + tracks[0].direccion }}
      .p
        strong Obligado a llevar contabilidad:
        | {{ ' ' + tracks[0].obligadoContabilidad }}
      .p(v-show="tracks[0].contribuyenteEspecial")
        strong Contribuyente Especial:
        | {{ ' ' + tracks[0].contribuyenteEspecial }}
</template>
<script>
import trackService from '@/services/track'

export default {
  data: () => ({
    tracks: [],
    isLoading: false,
    showNotification: false
  }),

  methods: {
    getDatosEmpresa () {
      this.isLoading = true
      trackService.contribuyente()
        .then(res => {
          // console.log(res)
          console.log(res.length)
          this.showNotification = res.length === 0
          this.tracks = res
          this.isLoading = false
        })
    }
  },
  beforeMount () {
    this.getDatosEmpresa()
  }
}
</script>
<style lang="scss" scoped>
</style>



