<template lang="pug">
  main
      transition(name="move")
        pm-warning(v-show = "showNotification")
          p(slot = "body")
            strong {{ mensaje }}
      nav.panel
        p.panel-heading
          |   Menú
        a.panel-block
          p
            router-link.nav-item(to = "factura") Facturas
        a.panel-block
          p
            router-link.nav-item(to = "retencion") Retenciones
        a.panel-block
          p
            router-link.nav-item(to = "notacredito") Notas de Crédito
        a.panel-block
          p
          |     Notas de Débito
        a.panel-block
          p
            router-link.nav-item(to = "guia") Guías de Remisión
        a.panel-block(v-show = "showNotification")
          router-link.button.button.is-warning.is-fullwidth(to = "pendiente")
            |
            strong Pendientes
        .panel-block
          router-link.button.button.is-link.is-outlined.is-fullwidth(to = "contacto") Contacto
</template>
<script>
import PmWarning from '@/components/shared/Warning.vue'

import trackService from '@/services/track'

export default {
  components: {
    PmWarning
  },

  data: () => ({
    tracks: [],
    mensaje: '',
    showNotification: false,
    numeroPendientes: 0
  }),

  methods: {
    getNumeroPendientes () {
      this.showNotification = false
      trackService.contarPendiente()
        .then(res => {
          this.tracks = res
          this.numeroPendientes = Number(this.tracks[0])
          console.log(`Número de pendientes: ${this.numeroPendientes}`)
          // True si es mayor a cero
          this.showNotification = this.numeroPendientes > 0
          if (this.showNotification) {
            this.mensaje = `Existen ${this.numeroPendientes} documentos pendientes de enviar al SRI o verificar`
          }
        })
    }
  },

  beforeMount () {
    this.getNumeroPendientes()
  }
}
</script>



